























import { Component, Vue } from 'vue-property-decorator'
import XLinks from './x-links.vue'
import { Direction } from './direction'
import { Datetime } from '../../domain/datetime'
import XLink from './x-link.vue'
import { Translate } from './translate'
import { Inject } from '../../domain/types/inject'
import { TYPES } from '../../types'

@Component({ name: 'x-footer', components: { XLink, XLinks } })
export default class XFooter extends Vue {
  direction = Direction.VERTICAL

  @Inject(TYPES.TRANSLATE)
  readonly translate!: Translate

  get coverageLiteral() {
    return this.translate('_coverage')
  }

  get coverage() {
    return process.env.VUE_APP_COVERAGE
  }

  get todaysYear() {
    return Datetime.fromNow().year
  }
}
